is"}},[_c('div',{staticClass:"row",attrs:{"titulo":"Frecuencia"}},[_c('div',{staticClass:"col-3 d-none d-md-block"},[_c('figure',{staticClass:"mb-2"},[_c('img',{attrs:{"src":require("@/assets/template/tema-1-71.png"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-12 col-md-8"},[_c('p',[_vm._v("Una vez que la empresa ha encontrado la combinación de medios más efectiva para llegar a su público objetivo, la siguiente pregunta que tiene que responder es: ¿con qué frecuencia debe repetir el mensaje, para conseguir un determinado nivel de notoriedad? Si la frecuencia es pequeña es posible que los mensajes no penetren en la mente del público objetivo y que los resultados de notoriedad y comprensión sean bajos. Por otra parte, si el mensaje se repite con demasiada frecuencia, los clientes potenciales podrían irritarse, lo que perjudicarían las percepciones de la publicidad, de los productos y de la propia compañía, "),_c('strong',[_vm._v("(Best, 2007)")]),_vm._v(".")]),_c('p',{staticClass:"mt-4"},[_vm._v("Para "),_c('strong',[_vm._v("Prettel (2016)")]),_vm._v(" la frecuencia debe determinarse de acuerdo con: ")]),_c('div',{staticClass:"row mt-3"},[_c('div',{staticClass:"col-sm mb-5 mb-sm-0"},[_c('ul',{staticClass:"lista-ul"},[_c('li',{staticClass:"mb-0"},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-1 text-align-right"},[_c('i',{staticClass:"fas fa-angle-right color-c text-center"})]),_c('div',{staticClass:"col-11"},[_c('p',[_c('strong',[_vm._v("Ciclo en que se encuentra el producto:")]),_vm._v(" en la etapa de introducción, la frecuencia de anuncios debe ser alta, por la necesidad de dar a conocer el producto y por la resistencia al cambio por parte de los consumidores. ")])])])]),_c('li',{staticClass:"mb-0"},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-1 text-align-right"},[_c('i',{staticClass:"fas fa-angle-right color-c text-center"})]),_c('div',{staticClass:"col-11"},[_c('p',[_c('strong',[_vm._v("Nivel de competencia: ")]